
.content {
    display: flex;
    height:100vh;

}
.left-content {
    display: flex;
    flex-direction: column;
    width: 12rem;
    background-color: rgb(11, 32, 15);
    padding-bottom: 25rem;
}
.left-down{
    display: flex;
    flex-direction: column;
    margin-top: 6rem;
    gap: 3rem;
    justify-content:center ;
   align-items: flex-start;
    margin-left:2rem ;
}
.left-down label {
    text-decoration: none;
    font-size: 1.3rem;
    color: white;
}

.list {
    display: flex;
    background-color: white;
    flex-direction: column;
    width: 8rem;
    gap: 0.3rem;
    border: 1px solid black;
    border-radius: 0.3rem;
    margin-top: -2.3rem;
    margin-left: -0.6em;
}
.list label {
    color: black;
    font-size: 1rem;
}
.admin {
    font-size: 2.0rem;
    color: white;
    font-weight: bolder;
}
.right-content {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 100%;
}
.nav {
    display: flex;
    flex-direction: column;
    gap: 10rem;
    width: 100%;
    height: 2rem;
    color:white;
    background-color: rgb(11, 32, 15);
    padding-left: 2rem;
    padding-right: 2rem;
    padding-top: 0.7rem;
}
.toggle {
    float: left;
}
.usercontent {
    display: flex;
    float: right;
    gap: 1rem;
}